@font-face { font-family: jello-icons; src: url("../fonts/jello-icons.eot"); src: url("../fonts/jello-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/jello-icons.woff2") format("woff2"), url("../fonts/jello-icons.woff") format("woff"), url("../fonts/jello-icons.ttf") format("truetype"); font-weight: normal; font-style: normal; }
body { display: flex; flex-direction: column; min-width: 1050px; font-size: 13px; line-height: 1.4; color: #37474f; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 400; }

h1 { font-size: 46px; color: #37474f; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }
h1.is-fz-32 { font-size: 32px; }

h3 { font-size: 18px; color: #37474f; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }

a { color: #0288d1; display: inline-block; text-decoration: none; cursor: pointer; transition: all 0.3s ease-in-out; }
a:active, a:focus, a:visited { text-decoration: none; }

img { display: inline-flex; max-width: 100%; height: auto; }

.logo { display: inline-block; }
.logo__img { width: 120px; }

.section { display: flex; }
.section__left { width: 71%; margin-right: 20px; }
.section__right { width: 29%; }

.aside__title { display: block; color: #37474f; letter-spacing: -0.3px; padding-bottom: 8px; border-bottom: 1px solid #d9dadb; }

.info__text { margin-bottom: 25px; }

.content { padding-top: 30px; }
.content__wrap { display: flex; flex-direction: row; justify-content: space-between; }
.content__left { width: 71%; margin-right: 20px; }
.content__right { width: 29%; }

.posts__list { margin-top: 10px; display: flex; flex-direction: column; }
.posts__el { margin-bottom: 15px; }
.posts__link { line-height: -0.8px; color: #37474f; }

.error { position: relative; padding-top: 110px; box-sizing: border-box; text-align: center; }
.error__name { font-size: 100px; margin-bottom: 30px; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }
.error__descr { margin-bottom: 30px; font-size: 16px; }

.btn-blue { width: 140px; height: 40px; line-height: 40px; text-align: center; color: #FFF; background-color: #3495df; font-size: 15px; border-radius: 0; transition: all 0.3s ease-in-out; cursor: pointer; }
.btn-blue:hover { background-color: #44a7f1; }

.btn { flex-direction: row; text-align: center; display: flex; flex-shrink: 0; width: 320px; height: 70px; line-height: 70px; color: #fff; background-color: #85cc16; font-size: 24px; white-space: nowrap; transition: all 0.3s ease-in-out; cursor: pointer; overflow: hidden; }
.btn:hover, .btn:focus, .btn:visited { text-decoration: none; }
.btn.is-search { display: block; text-align: center; width: 160px; height: 50px; line-height: 50px; font-size: 18px; border-radius: 0; border: none; text-transform: uppercase; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }
.btn.is-search:hover { background-color: #8cd120; }
.btn.is-search:focus { outline: none; }

.general-banner { text-align: center; margin-top: 80px; }
.general-banner__title { color: #ffffff; }
.general-banner__subtitle { margin-top: 20px; margin-bottom: 40px; font-size: 14px; color: #ffffff; }

.categories { margin-bottom: 30px; width: 300px; box-sizing: border-box; }
.categories__list { margin-top: 4px; display: flex; flex-direction: row; flex-wrap: wrap; }
.categories__el { width: 50%; height: 30px; line-height: 30px; overflow: hidden; white-space: nowrap; text-overflow: ellipsis; }
.categories__el:hover { background-color: #f2f4f5; }
.categories__link { color: #37474f; letter-spacing: -0.2px; transition: all 0.3s ease-in-out; }
.categories__link .icon { margin-right: 3px; vertical-align: middle; font-size: 18px; color: #66bb6a; }
.categories__link:hover { text-decoration: none; }
.categories.is-blue .icon { color: #42a5f5; }
.categories.is-orange .icon { color: #fb8c00; }

.ico { display: inline-block; vertical-align: middle; position: relative; transition: all 0.3s ease-in-out; }
.ico.is-app { background: url("../img/apps.svg") no-repeat; width: 16px; height: 16px; }
.ico.is-game { background: url("../img/game.svg") no-repeat; width: 16px; height: 16px; }
.ico.is-rington { background: url("../img/rington.svg") no-repeat; width: 16px; height: 16px; }
.ico.is-post { background: url("../img/post.svg") no-repeat; width: 16px; height: 16px; }
.ico.is-search { background: url("../img/ico_search.png") no-repeat; width: 18px; height: 18px; }

.information { margin-bottom: 75px; }
.information__el { margin: 10px 0; }
.information__title { display: block; line-height: 1.6; font-size: 18px; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }
.information__text { margin: 5px 0 8px; display: inline-block; }
.information .list { margin-top: 17px; }
.information .list__el { margin-bottom: 2px; }

.items { margin-bottom: 40px; }
.items__top { display: flex; flex-direction: row; justify-content: space-between; align-items: center; padding-bottom: 8px; border-bottom: 1px solid #d9dadb; }
.items__list { margin-top: 10px; display: flex; flex-direction: row; flex-wrap: wrap; justify-content: space-between; }
.items__el { width: 30%; margin-bottom: 16px; }
.items__link, .items__more { color: #37474f; }
.items.is-small .item { display: flex; flex-direction: column; }
.items.is-small .item__name { font-size: 13px; text-align: center; }

.item { display: flex; align-items: center; }
.item__left { margin-right: 15px; }
.item__name { display: block; font-size: 15px; color: #37474f; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }
.item__descr { color: #919090; }

.container { position: relative; margin: 0 auto; width: 1050px; }

.main { flex: 1 1 auto; position: relative; }

.header { position: relative; }
.header__bg { position: absolute; top: 0; bottom: 0; left: 0; right: 0; background: url("/img/backgr_main_page.png") right no-repeat; background-size: cover; }
.header__wrap { display: flex; flex-direction: column; justify-content: space-between; }
.header__top { display: flex; justify-content: space-between; height: 60px; line-height: 60px; }
.header__bottom { padding-top: 20px; padding-bottom: 80px; }

.footer { border-top: 1px solid #d9dadb; padding: 30px 0; }
.footer__wrap { display: flex; flex-direction: row; justify-content: space-between; align-items: center; align-content: center; }

.nav__list { display: flex; flex-direction: row; }
.nav__el { margin-right: 32px; letter-spacing: -0.3px; }
.nav__el:last-child { margin-right: 0; }
.nav__link { font-size: 16px; color: #ffffff; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 600; }
.nav__link:hover, .nav__link:active { text-decoration: underline; }
.nav.is-footer .nav__el { position: relative; padding-right: 20px; margin-right: 0; }
.nav.is-footer .nav__el:after { position: absolute; content: ""; top: 3px; right: 10px; width: 1px; height: 13px; background-color: #9ba1a4; }
.nav.is-footer .nav__el:last-child { padding-right: 0; }
.nav.is-footer .nav__el:last-child:after { display: none; }
.nav.is-footer .nav__link { font-size: 13px; color: #37474f; text-transform: capitalize; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 400; }

.search { position: relative; width: 250px; }
.search__form { display: flex; flex-direction: row; }
.search__input { display: block; padding-left: 10px; padding-right: 36px; width: 100%; height: 40px; line-height: 40px; background-color: #fff; border: 1px solid #d9dadb; color: #99a1a8; font-size: 16px; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 400; }
.search__ico { position: absolute; right: 10px; top: 10px; }
.search.is-general { width: 100%; }
.search.is-general .search__form { align-items: center; }
.search.is-general .search__ico { width: 18px; top: 16px; left: 16px; }
.search.is-general .search__input { padding-left: 45px; padding-right: 20px; width: 100%; height: 50px; line-height: 50px; font-size: 16px; color: #99a1a8; border: none; letter-spacing: -0.3px; box-sizing: border-box; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 400; }
.search.is-general .search__input:focus { outline: none; }


